/* Most CSS rules are in JS components */
body {
  margin: 0;
  padding: 0;
  font-family:
    'Roboto',
    'Open Sans',
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    Fira Sans,
    Droid Sans,
    Helvetica Neue,
    sans-serif;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#map-container {
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #333;
}
.mapboxgl-popup-content > .popup > * {
  margin: 0;
}
.map-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 4px;
  pointer-events: none;
}
.map-overlay-pane {
  display: block;
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
.map-title-and-search {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: auto;
}
.map-title {
  color: #fff;
  padding: 4px 0px;
  font-size: 16pt;
  font-family: 'Roboto', sans-serif;
  margin-bottom: 4px;
}
